.container {
  padding: 15px;
}

.homepage-btns .btn {
  width: 250px;
  margin: 0 auto;
  &:not(.visible) {
    opacity: 0.2;
    pointer-events: none;
  }
}

h2 {
  margin-bottom: 24px;
}

.modal {
  padding: 16px 0;
  button {
    max-width: 280px;
    margin: 0 auto 16px;
  }
}

.modal-dialog {
  width: 90%;
  margin: 0 auto;
}

.verified-question-container .selected {
  background-color: #0069d9;
  color: white;
}

.verified-question-container .good {
  border: 4px solid green;
}

.modal-body {
  text-align: center;
  p {
    margin-bottom: 0;
  }
}

.question-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;
  h4 {
    margin-bottom: 16px;
  }
  button {
    border: 1px solid gray;
    border-radius: 5px;
    background: none;
    text-align: left;
    margin: 4px 0;
    max-width: 500px;
    padding: 8px;
    &.active {
      background-color: #0069d9;
      color: white;
    }
    &:focus {
      outline: none;
    }
  }
}

.slide {
  width: 100%;
  height: 100%;
  display: flex;
}

.nav {
  display: flex;
  justify-content: space-between;
  max-width: 300px;
  margin: 0 auto;
}

.nav-wrapper {
  button {
    border-radius: 10px;
    padding: 8px 0;
    min-width: 140px;
  }
}
